<template>
	<div style="padding: 0;" >
		<a-card>
			<!-- 搜索框：根据设备名称查询-->
			<div class="search-group">
				<div class="search-item" style="float: left;">
					<span class="search-text">联系人姓名:</span>
					<a-input v-model="page.params.name" type="text" placeholder="可根据联系人姓名查询" />
				</div>
				<div class="search-item" style="float: left;">
					<span class="search-text">电话:</span>
					<a-input v-model="page.params.mobile" placeholder="请输入电话查询" />
				</div>

				<div class="search-item">
					<!-- 搜索按钮 -->
					<a-button type="primary" icon="search" @click="searchBySelect">
						Search
					</a-button>
				</div>
			</div>

			<a-divider style="margin-top:5px;"></a-divider>
			<!-- 表格 table -->
			<a-table rowKey="deviceId" :columns="columns" :pagination="false" :data-source="page.list" bordered :scroll="{ x: 1500, y: 560 }">
				<template slot="lineDirection" slot-scope="lineDirection">
					<span v-if="lineDirection == 1">上行往湖北方向 </span>
					<span v-if="lineDirection == 2"> 下行往河南方向 </span>
				</template>
				<template slot="identity" slot-scope="identity">
					<span v-if="identity==1">企业用户</span>
					<span v-if="identity==0">普通用户</span>
					<span v-if="identity==null">普通用户</span>
				</template>
				<template slot="scenepictures" slot-scope="text, record">
					<a-button type="primary" @click="showPic(record)">
						查看
					</a-button>
				</template>
				<template slot="status" slot-scope="text">
					<a-tag v-if="text === 0" color="#f50"> 已上报 </a-tag>
					<a-tag v-if="text === 1" color="#b91"> 已查看 </a-tag>
					<a-tag v-if="text === 2" color="#108ee9"> 已处理 </a-tag>
				</template>
				<template slot="operation" slot-scope="text, record">
					<a-space>
						<!-- <a-button type="primary" @click="handleDeviceEdit(record.deviceId)">
							修 改
						</a-button> -->
						<a-popconfirm title="你确定要删除这条数据嘛？" ok-text="是的" cancel-text="不，点错了" @confirm="handleRescueDelete(record.rescueId)">
							<a-button type="danger"> 删 除</a-button>
						</a-popconfirm>
					</a-space>
				</template>
			</a-table>
			<br />
			<!-- 表格 table 结束 -->
			<a-button type="primary"  style="float:left;margin-top: 15px;">总条数：{{page.totalCount}}</a-button><a-pagination  v-model="page.currentPage" :page-size-options="['5', '10', '20', '50']" :total="page.totalCount"
			show-size-changer :page-size="page.pageSize" @showSizeChange="onSizeChange" @change="onPageChange">
			</a-pagination>
		
		<div></div>
		<audio muted style="display: none;" id="audio" ref="audioDom" src="../../../assets/video/4611.mp3" controls="controls" loop="loop" autoplay="autoplay"></audio>

		<!-- 图片显示 -->
		<div>
			<a-modal title="事故详细信息"  height="580" :dialog-style="{ left: '-0px' }" :footer="null" @cancel="cancel()" :visible="visible" :confirm-loading="confirmLoading" >
				<a-row>
					<a-col :span="13" >
						<div style="margin-left: -40px;margin-top: 10px;float: left;">
							<a-row >
								<a-col :span="6" >
				<!-- 					<a-form-item   :label-col="formItemLayout.labelCol" :wrapper-col="formItemLayout.wrapperCol" label="用户ID">
										<a-input  :value="rescueInfo.userId"/>
									</a-form-item> -->
									<a-form-item   :label-col="formItemLayout.labelCol" :wrapper-col="formItemLayout.wrapperCol" label="紧急联系人">
										<a-input  :value="rescueInfo.name"/>
									</a-form-item>
									<a-form-item   :label-col="formItemLayout.labelCol" :wrapper-col="formItemLayout.wrapperCol" label="电话">
										<a-input  :value="rescueInfo.mobile"/>
									</a-form-item>
									<a-form-item   :label-col="formItemLayout.labelCol" :wrapper-col="formItemLayout.wrapperCol" label="事故车数量">
										<a-input  :value="rescueInfo.numVehicles"/>
									</a-form-item>
									<a-form-item   :label-col="formItemLayout.labelCol" :wrapper-col="formItemLayout.wrapperCol" label="里程桩号">
										<a-input  :value="rescueInfo.mileagePile"/>
									</a-form-item>
									<a-form-item   :label-col="formItemLayout.labelCol" :wrapper-col="formItemLayout.wrapperCol" label="处理状态">
										<a-input  v-if="rescueInfo.status===0" value="已上报"/>
										<a-input  v-if="rescueInfo.status===1" value="已查看"/>
										<a-input  v-if="rescueInfo.status===2" value="已处理"/>
									</a-form-item>
				
								</a-col>
								<a-col :span="6" >
									<a-form-item   :label-col="formItemLayout.labelCol" :wrapper-col="formItemLayout.wrapperCol" label="路线方向">
										<a-input v-if="rescueInfo.lineDirection===1"  value="上行往湖北方向"/>
										<a-input v-if="rescueInfo.lineDirection===2"  value="下行往河南方向"/>
									</a-form-item>
									<a-form-item   :label-col="formItemLayout.labelCol" :wrapper-col="formItemLayout.wrapperCol" label="经纬度坐标">
										<a-input  :value="rescueInfo.coordinates"/>
									</a-form-item>
									<a-form-item   :label-col="formItemLayout.labelCol" :wrapper-col="formItemLayout.wrapperCol" label="事故时间">
										<a-input  :value="rescueInfo.time"/>
									</a-form-item>
									<a-form-item   :label-col="formItemLayout.labelCol" :wrapper-col="formItemLayout.wrapperCol" label="角色">
										<a-input  v-if="rescueInfo.identity===0" value="普通用户"/>
										<a-input  v-if="rescueInfo.identity===1" value="企业用户"/>
										<a-input  v-if="rescueInfo.identity===null" value="普通用户"/>
									</a-form-item>
									<a-form-item   :label-col="formItemLayout.labelCol" :wrapper-col="formItemLayout.wrapperCol" label="注意事项">
										<a-input  :value="rescueInfo.atten"/>
									</a-form-item>
									<a-button style="position: absolute;left:140px" type="primary" @click="changeStatus()">已查看</a-button>
								</a-col>
								<a-col :span="1" >
									<div style="position: absolute;left: 180%;">
										<a-divider style="background-color:#DDDDDD;height:380px;" type="vertical" />
									</div>
								</a-col>
							</a-row>
						</div>
					</a-col>


					<a-col :span="11">
						<div v-if="pics.length!==0">
							<a-carousel arrows>
								<div slot="prevArrow" class="custom-slick-arrow" style="left: 10px;zIndex: 1">
									<a-icon type="left-circle" />
								</div>
								<div slot="nextArrow" class="custom-slick-arrow" style="right: 10px;">
									<a-icon type="right-circle" />
								</div>
								
								<div :key="item.rescueId" v-for="item in pics" style="width: 360px;height: 360px;">
									<img :src="item.url" style="width: 360px;height: 360px;zIndex:2!important;border: none;padding: 20px;" />
								</div>				
							</a-carousel>
						</div>
						<div v-if="pics.length===0" >
							<a-carousel arrows>
								<div slot="prevArrow" class="custom-slick-arrow" style="left: 10px;zIndex: 1">
									<a-icon type="left-circle" />
								</div>
								<div slot="nextArrow" class="custom-slick-arrow" style="right: 10px;">
									<a-icon type="right-circle" />
								</div>
								
								<div   style="width: 360px;height: 360px;" >
									<img src="../../../assets/wsg.jpg" style="width: 360px;height: 350px;zIndex:2!important;border: none;padding: 20px;" />
								</div>				
							</a-carousel>
						</div>
					</a-col>
				</a-row>
			</a-modal>
		</div>


</a-card>
	</div>
</template>

<script>
	const formItemLayout = {
		labelCol: {
			span: 4
		},
		wrapperCol: {
			span: 3
		},
	};
	// column 列封装
	import {
		getRescueList,
		deleleteById,
		updateStatus
	} from "@/api/Device.js";

	import {
		getPicById
	} from "@/api/scenePic.js";

	import {
		RescueColumns as columns
	} from "@/assets/js/columns/StandBook.js";
	// import XLSX from "xlsx";
	export default {
		name: "DeviceList",
		data() {
			return {
				formItemLayout,
				loading: true,
				// 搜索栏目
				confirmLoading: false,
				page: {
					currentPage: 1, // 当前页
					pageSize: 5, // 每页显示条数
					totalPage: 0, // 总页数
					totalCount: 0, // 总条数
					params: {
						name: "",
						mobile: ""
					}, // 查询参数对象
					list: [], // 数据
					sortColumn: "", // 排序列
					sortMethod: "", // 排序方式
				},
				//列名的数组
				columns: [],
				uploadDialogVisible: false,
				uploading: false,
				visible: false,
				pics: [],
				rescueInfo:{},
				conuts:0,
				flag:false
				// uploadUrl: "http://121.196.166.190/api/v1/private/oss/upload?deviceId=",
			};
		},
		created() {
			this.columns = columns;
			this.getRescueList();
			// this.handlePause()
			this.$message.success("查询成功");
					
		},
		mounted() {
			this.timer = setInterval(this.getRescueList, 6000);
		},
		methods: {
			handlePause () {
				this.$refs["audioDom"].pause();
			},
			handlePlay(){
				this.$refs["audioDom"].play();
			},
			async changeStatus(){				
				localStorage.setItem("counts",this.page.totalCount);
				this.handlePause();
				this.rescueInfo.status=1;
				const {
					data: res
				} = await updateStatus(this.rescueInfo);
				this.$message.success(res.msg);
			},
			async getPicList(rescueId) {				
				const {
					data: res
				} = await getPicById(rescueId);
				this.pics = res.data;
				this.$message.success(res.msg);
			},
			cancel() {
				this.visible = false
				this.getRescueList()
			},
			showPic(info) {
				// this.handlePlay()
				// this.visible=true
				this.rescueInfo=info								
				this.visible = true
				this.getPicList(info.rescueId);
			},
			searchBySelect() {
				this.getRescueList();
			},
			onPageChange(newPage) {
				this.page.currentPage = newPage;
				this.getRescueList();
			},
			// 页面变化
			onSizeChange(current, newSize) {
				this.page.pageSize = newSize;
				this.getRescueList();
			},
			// 获取设备列表
			async getRescueList() {
				const {
					data: res
				} = await getRescueList(this.page);
				console.log(res);
				if (res.code !== 20005) return this.$message.error("加载数据出错");
				this.page.list = res.data.list;
				this.page.totalCount = res.data.totalCount;
				if(this.flag!==false){
					if(parseInt(this.page.totalCount)>parseInt(localStorage.getItem("counts"))){
						this.handlePlay()
						// console.log("hahahah")
					}
				}else{
					this.handlePause()
					localStorage.setItem("counts",this.page.totalCount);
				}
				this.flag=true
				console.log(this.flag)
				console.log(this.page.totalCount)
				console.log(localStorage.getItem("counts"))
				this.loading = false;
			},
			async handleRescueDelete(rescueId) {
				const {
					data: res
				} = await deleleteById(rescueId);
				console.log(res);
				// 基于 ajax 删除
				let arr = this.page.list;
				arr.forEach((item, index, res) => {
					if (item.rescueId == rescueId) {
						res.splice(index, 1);
					}
				});
				this.page.list = arr;
			},
		},
	};
</script>

<style scoped>
	/deep/ .ant-modal-close-x {
		position: relative;
		right: 0px;
	}


	/deep/ .ant-row {
		width: 855px;
	}
	
	.search-group {
		margin-top: 30px;
		left:-150px;
		display: flex;
		justify-content: space-around;
		position: relative;
	}

	.search-item {
		margin-right: 22px;
	}

	.search-item input {
		width: 120px;
	}

	.search-text {
		font-size: 18px;
		font-family: Arial, Helvetica, sans-serif;
		font-weight: bold;
	}

	.search-btn {
		white-space: pre;
	}

	#export {
		float: right;
	}

	/* For demo */
	.ant-carousel>>>.slick-slide {
		text-align: center;
		height: 400px;
		line-height: 400px;
		overflow: hidden;
		background-color: #FFFFFF;
	}

	.ant-carousel>>>.custom-slick-arrow {
		width: 25px;
		height: 25px;
		font-size: 25px;
		color: #1890FF;
		background-color: rgba(155, 45, 61, 0.11);
		opacity: 0.3;

	}

	.ant-carousel>>>.custom-slick-arrow:before {
		display: none;
	}

	.ant-carousel>>>.custom-slick-arrow:hover {
		opacity: 0.5;
	}

	.ant-carousel>>>.slick-slide h3 {
		color: #364d79;
	}

	.ant-carousel>>>.slick-thumb li img {
		width: 100%;
		height: 100%;
		filter: grayscale(100%);
	}

	/deep/ .ant-carousel .slick-dots li.slick-active button {
		background: #1890FF;
	}

	.ant-carousel>>>.slick-slide img {
		border: 5px solid #fff;
		display: block;
		margin: auto;
		max-width: 80%;
	}
	/deep/ .ant-modal{
		width: 855px!important;
	}

	/deep/.ant-modal-body {
		max-width: 2000px;
		padding: 0px;
	}

	/deep/ .slick-actice>button {
		background-color: #1890FF;
	}
</style>
