import request from '../utils/http-utils.js'
// 和设备台账相关 API
const RescueApi = {
	RescueList: '/rescue/SelectInfo', // 分页 API
	OperateRescue: '/rescue' ,// 基础 CRUD API
}

// 分页获取 Rescue 列表
export function getRescueList (page) {
	return request({
		url: RescueApi.RescueList+"/page",
		method: 'post',
		data: page
	})
}


// 删除Rescue 列表信息
export function deleleteById (rescueId) {
	return request({
		url: RescueApi.OperateRescue+"/deleteById" + `/${rescueId}`,
		method: 'delete'
	})
}


// 改变状态码
export function updateStatus (data) {
	return request({
		url: RescueApi.OperateRescue+"/updateStatus",
		method: 'put',
		data:data
	})
}

